







import { Component, Prop, Watch, Vue } from 'vue-property-decorator';

@Component
export default class SolutionInserter extends Vue {
  /* DATA */

  private internalValue = '';

  /* PROPS */

  @Prop({ type: String, required: true })
  value!: string;

  /* WATCHERS */

  @Watch('value')
  watchSelectedFile() {
      this.internalValue = this.value;
  }
}
